* {
    margin: 0;
}

.navi {
    background-color: #fff;
    display: flex;
    align-items: center;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.navi__container {
    max-width: 1280px;
    height: 70px;
    margin: 0 auto;
    display: flex;
    align-items: center;
}

.navi__list {
    display: flex;
    gap: 60px;
}

.navi__list-link {
    text-decoration: none;
    font-size: 26px;
    color: black;
}
